@keyframes rise {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }
  50% {
    transform: translate(100px);
  }
  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}

// Container
.glass-container {
  @include glassmorphism;

  max-width: 1000px;
  max-height: 600px;
  display: grid;
  justify-content: space-around;
  align-items: center;
  grid-template-columns: 1fr 1fr;

  @media screen and (max-width: 900px) {
    grid-template-columns: 1fr;

    max-height: 800px;
    justify-content: center;
    align-items: center;
  }
}

.enter {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100vh;

  &-logo {
    width: 100%;
    padding: 2rem;
    padding-right: 0rem;
    @media screen and (max-width: 900px) {
      padding: 1rem;
    }
  }

  &-content {
    padding: 2rem;
    padding-left: 0rem;
    @media screen and (max-width: 900px) {
      padding: 1rem;
    }
  }
}

.buttons {
  margin-top: 5rem;
  display: flex;
  justify-content: space-around;

  @media screen and (max-width: 900px) {
    margin-top: 2rem;
  }
}
